import React, { useCallback, useEffect, useRef } from "react";
import styled from "@/styles/components/Button/index.module.scss";
function Button(props) {
  //props
  // onClick, style, children :  , isDebouncedButton :   
  const { onClick, style, children, isDebouncedButton = true } = props;
  //useRef
  const buttonRef = useRef(null);
  let timeout;
  //useCallback
  const debounce = useCallback((func, ms) => {
    //     timeout  id .

    return () => {
      //       .
      if (timeout) {
        clearTimeout(timeout);
      }

      // timeout   .  ms    .
      timeout = setTimeout(() => {
        func();
      }, ms);
    };
  }, []);
  return (
    <button
      ref={buttonRef}
      className={styled.button}
      style={style}
      onClick={isDebouncedButton ? debounce(onClick, 500) : onClick}
    >
      {children}
    </button>
  );
}
export default React.memo(Button);
